
html {
  box-sizing: border-box;
}
*, ::before, ::after {
  box-sizing: inherit;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
input {
  width: 100%;
  padding: 5px;
  font-size: 20px;
}
textarea {
  width: 100%;
  padding: 5px;
  font-size: 12px;
  height: 300px;
}



